body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden; }

.background-image {
  height: 100vh;
  background-color: black;
  background-image: url(/web-samuel/./static/media/header-background-bw.3566722b.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
  padding: 0; }

.container-text {
  background-color: rgba(0, 0, 0, 0.3);
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

.darker {
  background-color: rgba(0, 0, 0, 0.7); }

.outer-center {
  display: table;
  width: 100%; }

.middle-center {
  display: table-cell;
  vertical-align: middle; }

.inner-center {
  margin-left: auto;
  margin-right: auto;
  text-align: center; }

.text-style {
  font-family: 'Roboto', sans-serif;
  color: #fff;
  padding: 5%;
  padding-bottom: 0; }
  .text-style h1 {
    font-size: 5em;
    font-weight: 700; }
  .text-style h2 {
    font-size: 3em;
    font-weight: 700; }

.padding-up {
  padding-top: 2%;
  padding-bottom: 5%; }

.font-black {
  color: black; }

.color-font {
  background-color: #fdc029;
  color: black; }

.color-font-gray {
  background-color: #1b1c18; }

.yellow-background {
  background-color: #fdc029;
  padding: 0; }

.white-background {
  background-color: white;
  padding: 0; }

.full-height {
  height: 100vh;
  overflow: hidden; }

.partial-height {
  height: 80vh;
  display: inline; }

.photo {
  width: 100%;
  height: 100%;
  background-image: url(/web-samuel/./static/media/Samuel.0fca0e8c.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center; }

.container-info {
  margin: 20%; }

.connected-background {
  background-image: url(/web-samuel/./static/media/connected.fbcf4e24.jpg); }

.work-background {
  background-image: url(/web-samuel/./static/media/work-bw.4b8ceca5.jpg); }

.idea-background {
  background-image: url(/web-samuel/./static/media/idea.17e57f99.jpg); }

.button-section button {
  background-color: rgba(0, 0, 0, 0.5);
  margin: 3% 10px;
  border: none;
  color: white;
  border: 2px solid #fdc029;
  padding: 10px 40px;
  text-decoration: none; }

.button-section button:hover {
  background-color: #fdc029;
  color: black; }

.card-section {
  padding: 5% 5%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row; }

.message {
  padding: 10%; }

.chart-section {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  padding: 0 20px;
  margin-top: -60px; }

.card {
  width: 31.33333%;
  border: none; }
  .card:nth-child(n) {
    margin-bottom: 3%;
    margin-right: 3%; }
  .card:nth-child(3n) {
    margin-right: 0;
    margin-bottom: 0; }
  .card .image-placeholder {
    width: 100%;
    height: 300px;
    text-align: center;
    line-height: 300px; }
    .card .image-placeholder img {
      width: 100%;
      background-position: cover;
      vertical-align: middle; }

.banner-hover {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: .5s ease;
  background-color: #fdc029;
  border-radius: 5px;
  max-height: 300px; }

.card:hover .banner-hover {
  opacity: 1; }

.card-content {
  padding: 5%;
  text-align: center;
  color: black;
  font-family: 'Roboto', sans-serif;
  line-height: 1.5; }
  .card-content h3 {
    font-weight: 700;
    margin: 8% 0; }

path {
  stroke: black; }

line {
  stroke: black; }

text {
  fill: black; }

.recharts-wrapper {
  margin: 0 auto; }

.navbar {
  position: absolute;
  right: 0;
  left: 0; }

.navbar.transparent.navbar-inverse .navbar-inner {
  background-color: rgba(0, 0, 0, 0.4); }

